@import '~node_modules/modern-normalize/modern-normalize.css';
.visually-hidden {
    position: absolute;
    white-space: nowrap;
    width: 1px;
    height: 1px;
    overflow: hidden;
    border: 0;
    padding: 0;
    clip: rect(0000);
    clip-path: inset(50%);
    margin: -1px;
}
.is-hidden {
    visibility: hidden;
    opacity: 0;
    pointer-events: none;
}